import React from 'react'

const OpenSourceProjects = ( ) => {
    const opsProjects = [
        {
            img:'static/img/ops/graphiql-storm.png',
            title:'graphiql-storm',
            description:'A GraphQl web IDE'
        },
        {
            img:'static/img/ops/graphiql-storm.png',
            title:'no-backend',
            description:'A firebase like service but self-hosted'
        }  
    ]
    return (
    <div className='content-wrapper'>
        <div className="ops-container">
        {opsProjects.map((item,index)=>
            <div className="ops-card-container" key={index}>
                <div className="ops-card">
                    <div className="description">
                         <h3 className='text-center'>
                            {item.title}
                        </h3>
                        <span className='text-center'>
                            {item.description}
                        </span>
                    </div>
                    <img src={item.img} alt=""/>
                </div>
            </div>
        )}
        </div>
        <style jsx>{`
            .ops-container{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                margin-bottom: 150px;
            }
            .ops-card-container{
                display: flex;
                align-items: center;
                flex-direction: column;
                max-width:400px;
                margin:20px;

            }
            .ops-card{
                min-width:100px;
                min-height:100px;
                border-radius:24px;
                overflow: hidden;
                background: #fff;
                margin-bottom: 20px;
                box-sizing: border-box;
                display: flex;
                align-items: center;
                box-shadow: 0 10px 25px rgba(0,0,0,0.12), 9px 10px 45px rgba(0,0,0,0.24);
            }
            .description{
                padding:0px 15px;
            }
            img{
                object-fit: cover;
                width:100%;
                height: 100%;
                box-sizing: border-box;
            }
            .text-center{
                text-align: center;
            }
        `}</style>
    </div>
    )
}

export default OpenSourceProjects